
import React, { Component } from 'react'
import Logo from '../../../Image/logo-dark.png'
import {Link} from 'react-router-dom'

export default class Desktop extends Component {
    constructor(){
        super();
        this.state={
            subOpen:false,
            subOpen_Abt:false,
        }
    }
    removeSub=()=>{
        this.setState({subOpen:false})
    }
    removeSub_nav=()=>{
        this.setState({subOpen:false,subOpen_Abt:false})
    }
    needSub=()=>{
        this.setState({subOpen:true,subOpen_Abt:false})
    }
    needSub2=()=>{
        this.setState({subOpen_Abt:true,subOpen:false})
    }
    render() {
        const {subOpen} = this.state;
        const style = subOpen ?(null):({ display: "none"})
        const {subOpen_Abt} = this.state;
        const style2 = subOpen_Abt ?(null):({ display: "none"})
        return (
<div className='navigation-desk'>
          <Link className='logo-link' to='/'>
              <img className='navigation-logo' src={Logo} alt='IBTS Logo' />
          </Link> 
          <nav onMouseLeave={this.removeSub_nav}>
              <ul>
                  <li><Link onMouseEnter={this.removeSub_nav} className='menu-link' to=''>Home</Link></li>
                  <li><Link className='menu-link' onMouseEnter={this.needSub} to=''>Services</Link></li>
                  <li><Link onMouseEnter={this.removeSub_nav} className='menu-link' to='/portfolio'>Portfolio</Link></li>
                  <li><Link  onMouseEnter={this.needSub2} className='menu-link' to=''>About us</Link></li>
                  <li><Link onMouseEnter={this.removeSub_nav} className='menu-link' to='/blog'>Blog</Link></li>
                  <li><Link onMouseEnter={this.removeSub_nav} className='menu-link' to='/contact'>Contact</Link></li>
              </ul>
              <ul onMouseLeave={this.removeSub} style={style} className='sub-ul sub-ul-1'>
              <li><Link className='menu-link' to=''>Graphic Design</Link></li>
                  <li><Link className='menu-link' to=''>Web Development</Link></li>
              <li><Link className='menu-link' to=''>Artificial intelligence</Link></li>
                  <li><Link className='menu-link' to=''>
Business intelligence

</Link></li>
              </ul>
              <ul onMouseLeave={this.removeSub_nav} style={style2} className='sub-ul sub-ul-2'>
              <li><Link className='menu-link' to='/about'>About IBTSBD</Link></li>
                  <li><Link className='menu-link' to='/privacy-policy'>Privacy Policy</Link></li>
              <li><Link className='menu-link' to='/terms-condition'>Terms and Condition</Link></li>
              </ul>
          </nav> 
        </div>
        )
    }
}
